import React, { Component } from 'react';
import ColorPicker from "../../../components/color-picker/ColorPicker";
import HSLFilters from "./components/HSLFilters";
import TryIt from "../../../components/TryIt";

class HSL extends Component {
  render() {
    return (
      <div style={{ background: '#f5f5f5', display: 'inline-block', padding: '24px', margin: 'auto' }}>
        <div style={{ display: 'inline-block', verticalAlign: 'top' }}> <ColorPicker /> </div>
        <div style={{ display: 'inline-block', verticalAlign: 'top', margin: '0 48px' }}> <HSLFilters /> </div>
        <div style={{ display: 'inline-block', verticalAlign: 'top' }}><TryIt /></div>
      </div>
    );
  }
}

export default HSL;
